::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #adadaba4;

}

::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: #fff;
}

.highlight-row {
    background-color: rgb(113, 128, 215);
}